@import '../mixin/config.scss';
body,html {
    color: #0D364C;
    width: 100%;
    height: 100%;
    font-family: 'Noto Sans KR', sans-serif;
   //overflow-x:hidden;   
}
a {
    color: #425866;
    text-decoration: none;
    font-size: 15px;
}

em {
    font-style: normal;
}

h2 {
    color: $font-color;
    font-size: 30px;
    padding: 5px 0;
}

button {
    width: 200px;
    height: 40px;
    font-size: 14px;
    color: #333;
    text-align: center;
    line-height: 37px;
    border: 1px solid #cccccc;
    color: #808080;
    background-color: #fff;
    transition: 0.5s;
    letter-spacing: 1px;
    cursor: pointer;
    font-weight: bold;
}

button:hover {
    color: #fff !important;
    background-color: #333;
    border: 1px solid #333;
    transition: 0.5s;
}

.max__width {
    width: $max-width;
    margin: 0 auto;
    @include mobile {
        width: 100%;
        overflow: hideen;
    }
    @include tablet {
        width: 100%;
        overflow: hidden;
    }
}

.full__width {
    width: 100%;
}
// 말줄입표
.line__box {
    text-overflow: ellipsis;
    -o-text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    word-wrap: normal !important;
    display: block;
    height: 30px;
    line-height: 30px;
}